.comparison {
    &-table {
        padding-bottom: 60px;
        margin-top: -10px;
        overflow: hidden;
        &>div {
            position: relative;
        }
        &>.comparison-result {
            position: static;
            display: none;
            font-weight: 600;
        }
        &>.comparison-choose {
            display: none;
            margin-top: 20px;
            font-weight: 600;
        }
        table {
            border-collapse: separate;
            border-spacing: 0 5px;
            table-layout: fixed;
        }
        td {
            width: 380px;
            min-width: 380px;
            max-width: 380px;
            box-sizing: border-box;
            font-weight: 600;
            line-height: 1.3;
        }
        &__hovered {
            background-color: var(--compareBg);
        }
        tr {
            transition: all 0.2s;
            &:not(:first-child) {
                td {
                    padding: 18px 30px 17px;
                    border-top: 1px solid var(--Gainsboro);
                    border-bottom: 1px solid var(--Gainsboro);
                    &:first-child {
                        padding: 18px 19px 17px;
                        border-right: 1px solid var(--Gainsboro);
                        border-left: 1px solid var(--Gainsboro);
                    }
                    .popup-desc-parent {
                        display: inline-block;
                        margin: 0 0 0 4.5px;
                        padding: 2.2px 5px;
                        font-size: 11px;
                        color: var(--Wedgewood);
                        text-align: center;
                        line-height: 1;
                        vertical-align: top;
                        cursor: pointer;
                        transition: all 0.2s;
                        &:hover {
                            border-color: var(--Flamenco);
                            color: var(--Flamenco);
                        }
                        &:after {
                            content: "";
                            position: absolute;
                            left: 0.5px;
                            top: 0.7px;
                            display: inline-block;
                            height: 14px;
                            width: 14px;
                            border: 1px solid var(--Wedgewood);
                            border-radius: 50%;
                        }
                    }
                    .popup-desc {
                        width: 218px;
                        bottom: 23px;
                        top: auto;
                        left: -18px;
                        text-align: left;
                        z-index: 1;
                        span {
                            color: var(--Red);
                        }
                    }
                }
            }
            &:first-child {
                td {
                    padding: 10px 20px 25px 0;
                    vertical-align: top;
                    &:first-child {
                        position: relative;
                        vertical-align: bottom;
                    }
                }
            }
        }
        .card-full {
            &:hover {
                .card-bottom {
                    box-shadow: none;
                }
            }
        }
        .basket__item-btn {
            position: absolute;
            top: 14px;
            right: 15px;
            z-index: 1;
            i {
                font-size: 12px;
            }
        }
        .card-img {
            margin-top: 40px;
        }
        .card-bottom .count__wrap {
            opacity: 1;
            visibility: visible;
            height: 59px;
            margin-bottom: 18px;
        }
        .card-bottom {
            position: static;
            border: none;
            width: 100%;
        }
        .card-body {
            padding-bottom: 0;
        }
        .card-body .card-action {
            margin-bottom: 15px;
        }
        .card-body .card-link {
            margin-bottom: 20px;
            height: 46px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
        }
        .card-action__bottom {
            line-height: 1;
        }
        .card-body .price {
            margin-bottom: 18px;
        }
        .mCustomScrollbar,
        .mCustomScrollbar.mCS_touch_action,
        .mCustomScrollbar.mCS_touch_action .mCS-aviva.mCSB_scrollTools {
            touch-action: auto;
        }
        .mCustomScrollBox.mCSB_horizontal {
            touch-action: pan-y;
        }
        .mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_dragger .mCSB_dragger_bar {
            height: 8px;
            margin: 0;
            background-color: var(--Flamenco);
        }
        .mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_draggerRail {
            height: 1px;
            margin: 3.5px 0;
            background-color: var(--Gainsboro);
        }
        .mCustomScrollBox>.mCSB_scrollTools {
            opacity: 1;
        }
        .mCSB_horizontal.mCSB_inside>.mCSB_container {
            margin-bottom: 45px;
        }
        .mCSB_scrollTools.mCSB_scrollTools_horizontal {
            height: 18px;
        }
    }
    &-title, &-body {
        width: 100%;
    }
    &-result {
        position: absolute;
        top: 8px;
        left: 0;
        color: var(--Charcoal);
        font-weight: 400;
    }
    &-none {
        position: relative;
        &:after {
            position: absolute;
            content: "";
            height: 1px;
            width: 28px;
            background-color: var(--mainColor);
            top: calc(50% - 0.5px);
            left: 30px;
        }
    }
    &-title {
        position: absolute;
        left: 0;
        top: 0;
        width: 380px;
        td {
            opacity: 0;
            visibility: hidden;
            &:first-child {
                opacity: 1;
                visibility: visible;
            }
        }
    }
    &-body {
        width: calc(100% - 380px);
        margin-left: 380px;
        td:first-child {
            visibility: hidden;
        }
        tr:not(:first-child) td:last-child {
            border-right: 1px solid var(--Gainsboro);
        }
        table {
            margin-left: -380px;
        }
    }
}
@media(max-width: 1650px) {
    .comparison-table {
        .card-img {
            height: 12vw;
        }
    }
}
@media(max-width: 1550px) {
    .comparison {
        &-table {
            padding-bottom: 30px;
            /* .card-full .card-img {
                height: 10.15vw;
            } */
            td {
                width: 245px;
                min-width: 245px;
                max-width: 245px;
            }
            tr:first-child td {
                padding-right: 10px;
            }
            .card-img {
                margin-top: 30px;
                margin-bottom: 5px;
            }
            .basket__item-btn {
                top: 5px;
                right: 10px;
            }
            .mCSB_horizontal.mCSB_inside>.mCSB_container {
                margin-bottom: 45px;
            }
        }
        &-title {
            width: 245px;
        }
        &-body {
            width: calc(100% - 245px);
            margin-left: 245px;
            table {
                margin-left: -245px;
            }
        } 
    }
}
