@media(max-width: 575px) {
    .comparison {
        &-table {
            margin-top: 0;
            &>.comparison-result {
                display: block;
            }
            &>.comparison-choose {
                display: block;
            }
            &>div {
                margin-top: 20px;
                .comparison-result {
                    display: none;
                }
                .comparison-choose {
                    display: none;
                }
            }
            td {
                width: 145px;
                min-width: 145px;
                max-width: 145px;
            }
            tr:first-child td {
                padding: 0 10px 15px 0;
            }
            tr:not(:first-child) td, tr:not(:first-child) td:first-child {
                padding: 18px 10px 17px;
            }
            .card-img {
                height: 82px;
            }
            .card-action__bottom {
                flex-direction: column;
                padding: 0 10px;
            }
            .card-body .card-link {
                height: 132px;
                -webkit-line-clamp: 6;
            }
            .card-body .card-action {
                margin-bottom: 10px;
            }
            .card-bottom .count__wrap {
                margin-bottom: 10px;
            }
            .card-bottom .card-action__bottom .buyFast {
                margin: 0 0 10px 0;
                padding-left: 7px;
                padding-right: 7px;
            }
            .basketBtn {
                justify-content: center;
            }
            .radio:not(:last-child) {
                margin-bottom: 15px;
            }
            .mCSB_horizontal.mCSB_inside>.mCSB_container {
                margin-bottom: 40px;
            }
        }
        &-title {
            width: 145px;
        }
        &-body {
            width: calc(100% - 145px);
            margin-left: 145px;
            table {
                margin-left: -145px;
            }
        }
        &-none {
            &:after {
                left: 10px;
            }
        }
    }
}